import React, { useState, useEffect, useRef, useCallback } from "react"
import { useHistory } from "react-router-dom"
import styled from "styled-components"
import { useSelector } from "react-redux"
import { dbService, storageService } from "../../../fireBase"
import RemoveCircleIcon from "@material-ui/icons/RemoveCircle"
import { trim } from "utils/util"
import Layout from "../../common/Layout"

const Edit = (props) => {
  const { talkSeq } = props.match.params
  const history = useHistory()
  const { user } = useSelector((state) => state.userReducer)
  const [uid, setUid] = useState(0)
  const [title, setTitle] = useState("")
  const [contents, setContents] = useState("")
  //   {base64: null, file: null}
  const [file, setFile] = useState(null)
  const [fileData, setFileData] = useState("")
  const inputRef = useRef()

  useEffect(() => {
    const initData = async () => {
      const result = await dbService.collection("talks").doc(talkSeq).get()
      const data = result.data()
      setUid(data.uid)
      setTitle(data.title)
      setContents(data.contents)
      setFileData(data.imageUrl)
    }

    initData()
  }, [])

  const onChangeInputFile = useCallback(async (e) => {
    if (e.target.files[0]) {
      const reader = new FileReader()

      reader.onload = (event) => {
        setFileData(event.target.result)
      }
      setFile(e.target.files[0])
      reader.readAsDataURL(e.target.files[0])
    }
  }, [])

  const removeFile = () => {
    setFile(null)
    setFileData("")
  }

  const onSubmit = useCallback(async () => {
    if (uid !== user.uid) return alert("You can't edit it. because you are not a writer")
    else if (trim(title) == "") return alert("Please enter a title")
    else if (trim(contents) == "") return alert("Please enter a contents")

    let fileUrl = ""
    if (fileData.includes("https://firebasestorage.googleapis.com")) {
      fileUrl = fileData
    } else if (fileData !== "" && file !== null) {
      const fileRef = storageService.ref().child(`talks/${file.name}`)
      const response = await fileRef.putString(fileData, "data_url")
      fileUrl = await response.ref.getDownloadURL()
    }

    await dbService
      .collection("talks")
      .doc(talkSeq)
      .update({
        title,
        contents,
        imageUrl: fileUrl,
      })
      .then(() => history.goBack())
  }, [title, contents, file, fileData])

  return (
    <Layout>
      <InputSection>
        <Input type={"text"} placeholder={"Title"} value={title} onChange={(e) => setTitle(e.target.value)} />
        <Textarea placeholder={"Contents"} value={contents} onChange={(e) => setContents(e.target.value)}></Textarea>
        <LeftTextView error={contents.length > 500}>
          <p>{contents.length}/500</p>
        </LeftTextView>
      </InputSection>

      <ImageSection>
        <input ref={inputRef} style={{ display: "none" }} type={"file"} onChange={onChangeInputFile} accept={"image/*"} />
        <ImageAddButton onClick={() => inputRef.current.click()}>Add image</ImageAddButton>

        <ImageListView>
          {fileData && (
            <ImageItem>
              <img src={fileData} alt="image" />
              <button onClick={removeFile}>
                <RemoveCircleIcon />
              </button>
            </ImageItem>
          )}
        </ImageListView>
      </ImageSection>

      <ButtonsSection>
        <Button margin={true} onClick={() => history.goBack()}>
          <p>cancel</p>
        </Button>
        <Button onClick={onSubmit}>
          <p>edit</p>
        </Button>
      </ButtonsSection>
    </Layout>
  )
}

export default Edit

const InputSection = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  @media (max-width: 1024px) {
    width: 100%;
    padding: 0 10px;
  }
  @media (max-width: 768px) {
  }
`
const Input = styled.input`
  width: 100%;
  height: 40px;
  padding: 5px 10px;
  font-size: 20px;
`
const Textarea = styled.textarea`
  width: 100%;
  height: 400px;
  margin-top: 20px;
  padding: 5px 10px;
  font-size: 20px;
`
const LeftTextView = styled.div`
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: fit-content;
  & > p {
    margin-top: 5px;
    color: ${(props) => (props.error ? "#ff0000" : "#000")};
  }
`
// IMAGE
const ImageSection = styled.section`
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  margin-top: 20px;
  @media (max-width: 1024px) {
    width: 100%;
    padding: 0 10px;
  }
  @media (max-width: 768px) {
    flex-direction: column;
  }
`
const ImageAddButton = styled.button`
  width: fit-content;
  height: fit-content;
  margin-right: 20px;
  padding: 5px;
  border: 1px solid;
  border-radius: 5px;
  font-weight: bold;
`
const ImageListView = styled.div`
  flex: 1;
  width: 100%;
  &::after {
    content: "";
    clear: both;
    display: block;
  }
  @media (max-width: 1024px) {
  }
  @media (max-width: 768px) {
    margin-top: 10px;
  }
`
const ImageItem = styled.div`
  position: relative;
  float: left;
  width: 150px;
  height: 150px;
  margin-right: 10px;
  & > img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover;
  }
  & > button {
    background-color: #00000000;
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 0px;
  }
  @media (max-width: 1024px) {
  }
  @media (max-width: 768px) {
    width: 33.3%;
    height: auto;
    margin-right: 0;
    &::after {
      content: "";
      padding-bottom: 100%;
      display: block;
    }
    & > img {
      position: absolute;
    }
  }
`
// BUTTONS
const ButtonsSection = styled.section`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
`
const Button = styled.button`
  width: fit-content;
  height: fit-content;
  margin-right: ${(props) => (props.margin ? 10 : 0)}px;
  padding: 5px 10px;
  border: 1px solid;
  border-radius: 5px;
  & > p {
    font-size: 20px;
  }
  @media (max-width: 1024px) {
  }
  @media (max-width: 768px) {
    & > p {
      font-size: 15px;
    }
  }
`
